import React, { Component } from 'react';
import { Row, Col, Input, Modal, Button,
    Form,
    Tooltip,
    Icon,
    Cascader,
    Select,
    Checkbox,
    AutoComplete, } from 'antd';
import '../static/css/app.css'

const { Option } = Select;
const AutoCompleteOption = AutoComplete.Option;

class login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: {
                bottom: 20,
                size: 16
            },
            password: {
                bottom: 20,
                size: 16
            },
            user: {
                username: '',
                password: ''
            },
            visible: false,
            confirmLoading: false,
        };
    }
    focusInput = (e) => {
        if (e === 0) {
            this.setState({
                name: {
                    bottom: 50,
                    size: 12
                }
            })
        } else {

            this.setState({
                password: {
                    bottom: 50,
                    size: 12
                }
            })
        }
    }
    blurInput = (e) => {
        let { user } = this.state;
        if (e === 0 && user.username === '') {

            this.setState({
                name: {
                    bottom: 20,
                    size: 16
                }
            })
        } else if (user.password === '') {

            this.setState({
                password: {
                    bottom: 20,
                    size: 16
                }
            })
        }
    }

    changeInput = (name, e) => {
        console.log(name, e.target.value);
        let { user } = this.state;
        user[name] = e.target.value;
        this.setState({
            user
        })
    }

    showModal = () => {
        this.setState({
            visible: true,
        });
    };

    handleOk = () => {
        this.setState({
            ModalText: 'The modal will be closed after two seconds',
            confirmLoading: true,
        });
        setTimeout(() => {
            this.setState({
                visible: false,
                confirmLoading: false,
            });
        }, 2000);
    };

    handleCancel = () => {
        console.log('Clicked cancel button');
        this.setState({
            visible: false,
        });
    };

    regHeader() {
        return (
            <div>
                <h2>注册</h2>
                <p>
                    创建一个账号去加入我们
                </p>
            </div>
        )
    }

    render() {
        const formItemLayout = {
            labelCol: {
              xs: { span: 24 },
              sm: { span: 8 },
            },
            wrapperCol: {
              xs: { span: 24 },
              sm: { span: 16 },
            },
          };
        const { visible, confirmLoading } = this.state;
        return (
            <Row type="flex" justify="center" align="middle" className="login">
                <Col xs={20} sm={16} md={14} lg={12} xl={7}>

                    <div className="login-box">
                        <div className="login-input">
                            <div className="login-logo">
                                <h2>LOGIN</h2>
                            </div>
                            <div className="input-box">
                                <label className="name-label">账号</label>
                                <Input allowClear defaultValue={this.state.user.username} onFocus={this.focusInput.bind(this, 0)} onBlur={this.blurInput.bind(this, 0)} onChange={this.changeInput.bind(this, 'username')} />
                            </div>
                            <div className="input-box">
                                <label className="pwd-label" >密码</label>
                                <Input.Password defaultValue={this.state.user.password} onFocus={this.focusInput.bind(this, 1)} onBlur={this.blurInput.bind(this, 1)} onChange={this.changeInput.bind(this, 'password')} />
                            </div>
                            <a href="#" className="forget-pwd">忘记密码？</a>
                        </div>
                        <div className="login-btn">
                            <button className="logon-btns" onClick={this.showModal}>
                                注册
                            </button>
                            <button className="login-btns">
                                登录
                            </button>
                        </div>
                    </div>
                    <Modal
                        title={<this.regHeader />}
                        visible={visible}
                        footer={null}
                        onCancel={this.handleCancel}
                    >
                        <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                            <Form.Item label="E-mail">
                                {getFieldDecorator('email', {
                                    rules: [
                                        {
                                            type: 'email',
                                            message: 'The input is not valid E-mail!',
                                        },
                                        {
                                            required: true,
                                            message: 'Please input your E-mail!',
                                        },
                                    ],
                                })(<Input />)}
                            </Form.Item>
                            <Form.Item label="Password" hasFeedback>
                                {getFieldDecorator('password', {
                                    rules: [
                                        {
                                            required: true,
                                            message: 'Please input your password!',
                                        },
                                        {
                                            validator: this.validateToNextPassword,
                                        },
                                    ],
                                })(<Input.Password />)}
                            </Form.Item>
                            <Form.Item label="Confirm Password" hasFeedback>
                                {getFieldDecorator('confirm', {
                                    rules: [
                                        {
                                            required: true,
                                            message: 'Please confirm your password!',
                                        },
                                        {
                                            validator: this.compareToFirstPassword,
                                        },
                                    ],
                                })(<Input.Password onBlur={this.handleConfirmBlur} />)}
                            </Form.Item>
                            <Form.Item
                                label={
                                    <span>
                                        Nickname&nbsp;
              <Tooltip title="What do you want others to call you?">
                                            <Icon type="question-circle-o" />
                                        </Tooltip>
                                    </span>
                                }
                            >
                                {getFieldDecorator('nickname', {
                                    rules: [{ required: true, message: 'Please input your nickname!', whitespace: true }],
                                })(<Input />)}
                            </Form.Item>
                           
                            <Form.Item {...tailFormItemLayout}>
                                <Button type="primary" htmlType="submit">
                                    Register
                                </Button>
                            </Form.Item>
                        </Form>
                    </Modal>
                </Col>
                <style jsx>
                    {
                        `
                        .input-box .name-label{
                            position: absolute;
                            bottom: ${this.state.name.bottom}px;
                            left: 10px;
                            z-index: 10;
                            font-size: ${this.state.name.size}px;
                        }
                        .input-box .pwd-label{
                            position: absolute;
                            bottom: ${this.state.password.bottom}px;
                            left: 10px;
                            z-index: 10;
                            font-size: ${this.state.password.size}px;
                        }
                        `
                    }
                </style>
            </Row>
        );
    }
}

export default login;